import React, { useState } from 'react';
import '../style/css_animation.css';
import styles from '../style/Home.module.css';
import { Link } from 'react-router-dom';

const Home = () => {
  const vid1 = process.env.PUBLIC_URL + '/vid/title_logo_particle.webm';
  const vid2 = process.env.PUBLIC_URL + '/vid/title_logo_img.webm';
  const vid3 = process.env.PUBLIC_URL + '/vid/fire_overlay.webm';
  const vid4 = process.env.PUBLIC_URL + '/vid/fire_particle_loop.webm';
  //버튼 hover 구현
  const [isHovered, setIsHovered] = useState(false);
  const btn_start = process.env.PUBLIC_URL + '/img/ui/btn_start.webp';
  const btn_hover = process.env.PUBLIC_URL + '/img/ui/btn_start_hover.webp';
  //
  return (
    <div className={styles.section}>
      <div className={styles.container}>
        <div className={styles.wrapper}>
          <div className={styles.vid__1}>
            <video muted autoPlay>
              <source src={vid1} type="video/webm" />
            </video>
          </div>
          <div className={styles.vid__2}>
            <video muted autoPlay>
              <source src={vid2} type="video/webm" />
            </video>
          </div>
          <div className={styles.vid__3}>
            <video muted autoPlay>
              <source src={vid3} type="video/webm" />
            </video>
          </div>
          <div className={styles.vid__4}>
            <video muted autoPlay loop>
              <source src={vid4} type="video/webm" />
            </video>
          </div>
        </div>
        <div className={styles.wrapper__btn}>
          <Link to="/Start">
            <div
              className={styles.btn}
              style={{
                backgroundImage: `url(${isHovered ? btn_hover : btn_start})`,
              }}
              onMouseEnter={() => setIsHovered(true)}
              onMouseLeave={() => setIsHovered(false)}
            >
              시작하기
            </div>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Home;
