import React, { useState } from 'react';
import styles from '../style/Zodiac.module.css';
import { Link } from 'react-router-dom';

const Zodiac = () => {
  //버튼 hover 구현
  const [isHovered, setIsHovered] = useState(false);
  const btn_start = process.env.PUBLIC_URL + '/img/ui/btn_start.webp';
  const btn_hover = process.env.PUBLIC_URL + '/img/ui/btn_start_hover.webp';
  //
  const [selectedCard, setselectedCard] = useState(null);
  const [setValue, setArray] = useState(null);
  //스몰 카드 클릭용
  const handleSmallCardClick = (card) => {
    setselectedCard(selectedCard === card ? null : card);
  };
  //다음으로 넘어갈 때 데이터 저장
  const saveAndNext = () => {
    if (selectedCard) {
      //데이터 저장('데이터 이름', '데이터')
      localStorage.setItem('zodiac__selected', selectedCard);
    }
  };

  // 스몰 카드
  const names = [
    '쥐',
    '소',
    '호랑이',
    '토끼',
    '용',
    '뱀',
    '말',
    '양',
    '원숭이',
    '닭',
    '개',
    '돼지',
  ];
  const descs = [
    '자(子)',
    '축(丑)',
    '인(寅)',
    '묘(卯)',
    '진(辰)',
    '사(巳)',
    '오(午)',
    '미(未)',
    '신(申)',
    '유(酉)',
    '술(戌)',
    '해(亥)',
  ];

  //값 저장용
  const findIndex = () => {
    const index = names.findIndex((item) => item === selectedCard);
    if (index !== -1) {
      setArray(index);
      localStorage.setItem('zodiac__value', index);
    }
  };

  return (
    <div className={styles.section}>
      <div className={styles.container}>
        <div className={styles.wrapper__text}>
          <h1>좋아하는 동물을 선택하세요</h1>
          <h3>
            띠를 선택하거나, 원하는 형상을 선택하세요. <br />이 단계에서 무기의
            외형이 결정됩니다
          </h3>
        </div>
        {/* 내부 스몰 카드들 */}
        <div className={styles.wrapper}>
          {names.map((card, index) => (
            <div
              key={card}
              className={`${styles.zodiac__card} ${
                selectedCard === card ? styles.selected : ''
              }`}
              onClick={() => handleSmallCardClick(card)}
            >
              <h3>{card}</h3>
              <p>{descs[index]}</p>
            </div>
          ))}
        </div>
        {/* 다음 넘어가기 버튼 */}
        {selectedCard ? (
          <Link
            to="/Constell"
            className={`${styles.btn} ${styles.active}`}
            style={{
              backgroundImage: `url(${isHovered ? btn_hover : btn_start})`,
            }}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
            onClick={(saveAndNext, findIndex)}
          >
            결정하기
          </Link>
        ) : (
          <div
            className={styles.btn}
            style={{
              backgroundImage: `url(${btn_start})`,
            }}
          >
            선택을 기다리는 중
          </div>
        )}
      </div>
    </div>
  );
};

export default Zodiac;
